@import "../scss/variables";
@media (min-width: 1025px) {
    .boxed {
        .page {
            width: 1170px;
            margin: 0 auto;
            background: #f2f6f9;
            position: relative;
            display: -ms-flexbox;
            display: -webkit-box;
            display: flex;
            -ms-flex-direction: column;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            flex-direction: column;
            -ms-flex-pack: center;
            -webkit-box-pack: center;
            justify-content: center;
            min-height: 100vh;
            -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px, rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
            box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px, rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
        }
        .header.top-header {
            width: 1170px !important;
        }
        .hor-menu.horizontal-main {
            max-width: 1170px;
        }
        .main-content {
            .container,
            .container-fluid {
                padding-left: 20px;
                padding-right: 20px;
            }
        }
        .main-header> {
            .container,
            .container-fluid {
                padding-left: 15px;
                padding-right: 15px;
            }
        }
        .app-sidebar {
            right: auto !important;
        }
        .side-header {
            left: auto !important;
            right: auto !important;
            z-index: 1044;
        }
        .main-header.side-header {
            max-width: 1170px;
        }
        .nav-tabs .nav-link {
            padding: 1rem 1.2rem;
        }
        .app.sidenav-toggled .app-sidebar.comb-sidebar {
            right: -230px !important;
        }
        .horizontalMenu>.horizontalMenu-list>li:last-child>ul.sub-menu {
            left: 0;
        }
        .page-style1 .page-content {
            z-index: 999;
        }
        .app-header {
            padding-left: 10px !important;
        }
        .second-sidemenu {
            right: auto !important;
            margin-right: 70px;
        }
        .app.sidebar-mini.sidenav-toggled4 .first-sidemenu {
            right: auto !important;
        }
    }
    body.boxed {
        background: #bdc0c8 !important;
    }
}

@media only screen and (max-width: 1162px) and (min-width: 992px) {
    .boxed .horizontalMenu>.horizontalMenu-list>li>a {
        font-size: 12px;
        padding: 10px 5px 10px 5px !important;
    }
}

.boxed .app-header.header.top-header .profile-dropdown {
    padding-left: 0;
}

.boxed .today-profit {
    margin-right: 30px;
}